/* eslint-disable */

/** tzv */
export enum Unnamed8449 {
  NO_RESTRICTION = "NO_RESTRICTION",
  SW_DEVICE = "SW_DEVICE",
  LATCHSKY_DEVICE = "LATCHSKY_DEVICE",
  UNRECOGNIZED = "UNRECOGNIZED",
}

export function unnamed8449FromJSON(object: any): Unnamed8449 {
  switch (object) {
    case 0:
    case "NO_RESTRICTION":
      return Unnamed8449.NO_RESTRICTION;
    case 1:
    case "SW_DEVICE":
      return Unnamed8449.SW_DEVICE;
    case 2:
    case "LATCHSKY_DEVICE":
      return Unnamed8449.LATCHSKY_DEVICE;
    case -1:
    case "UNRECOGNIZED":
    default:
      return Unnamed8449.UNRECOGNIZED;
  }
}

export function unnamed8449ToJSON(object: Unnamed8449): string {
  switch (object) {
    case Unnamed8449.NO_RESTRICTION:
      return "NO_RESTRICTION";
    case Unnamed8449.SW_DEVICE:
      return "SW_DEVICE";
    case Unnamed8449.LATCHSKY_DEVICE:
      return "LATCHSKY_DEVICE";
    case Unnamed8449.UNRECOGNIZED:
    default:
      return "UNRECOGNIZED";
  }
}

export function unnamed8449ToNumber(object: Unnamed8449): number {
  switch (object) {
    case Unnamed8449.NO_RESTRICTION:
      return 0;
    case Unnamed8449.SW_DEVICE:
      return 1;
    case Unnamed8449.LATCHSKY_DEVICE:
      return 2;
    case Unnamed8449.UNRECOGNIZED:
    default:
      return -1;
  }
}
