/** uas */
export enum Unnamed8464 {
  OTHER = "OTHER",
  PHONE = "PHONE",
  TABLET = "TABLET",
  TV = "TV",
  GLASS = "GLASS",
  CAR = "CAR",
  WEARABLE = "WEARABLE",
  THINGS = "THINGS",
  CHROMEOS_ARC = "CHROMEOS_ARC",
  BSTAR = "BSTAR",
  UNRECOGNIZED = "UNRECOGNIZED",
}

export function unnamed8464FromJSON(object: any): Unnamed8464 {
  switch (object) {
    case 1:
    case "OTHER":
      return Unnamed8464.OTHER;
    case 2:
    case "PHONE":
      return Unnamed8464.PHONE;
    case 3:
    case "TABLET":
      return Unnamed8464.TABLET;
    case 4:
    case "TV":
      return Unnamed8464.TV;
    case 5:
    case "GLASS":
      return Unnamed8464.GLASS;
    case 6:
    case "CAR":
      return Unnamed8464.CAR;
    case 7:
    case "WEARABLE":
      return Unnamed8464.WEARABLE;
    case 8:
    case "THINGS":
      return Unnamed8464.THINGS;
    case 9:
    case "CHROMEOS_ARC":
      return Unnamed8464.CHROMEOS_ARC;
    case 10:
    case "BSTAR":
      return Unnamed8464.BSTAR;
    case -1:
    case "UNRECOGNIZED":
    default:
      return Unnamed8464.UNRECOGNIZED;
  }
}

export function unnamed8464ToJSON(object: Unnamed8464): string {
  switch (object) {
    case Unnamed8464.OTHER:
      return "OTHER";
    case Unnamed8464.PHONE:
      return "PHONE";
    case Unnamed8464.TABLET:
      return "TABLET";
    case Unnamed8464.TV:
      return "TV";
    case Unnamed8464.GLASS:
      return "GLASS";
    case Unnamed8464.CAR:
      return "CAR";
    case Unnamed8464.WEARABLE:
      return "WEARABLE";
    case Unnamed8464.THINGS:
      return "THINGS";
    case Unnamed8464.CHROMEOS_ARC:
      return "CHROMEOS_ARC";
    case Unnamed8464.BSTAR:
      return "BSTAR";
    case Unnamed8464.UNRECOGNIZED:
    default:
      return "UNRECOGNIZED";
  }
}

export function unnamed8464ToNumber(object: Unnamed8464): number {
  switch (object) {
    case Unnamed8464.OTHER:
      return 1;
    case Unnamed8464.PHONE:
      return 2;
    case Unnamed8464.TABLET:
      return 3;
    case Unnamed8464.TV:
      return 4;
    case Unnamed8464.GLASS:
      return 5;
    case Unnamed8464.CAR:
      return 6;
    case Unnamed8464.WEARABLE:
      return 7;
    case Unnamed8464.THINGS:
      return 8;
    case Unnamed8464.CHROMEOS_ARC:
      return 9;
    case Unnamed8464.BSTAR:
      return 10;
    case Unnamed8464.UNRECOGNIZED:
    default:
      return -1;
  }
}
